                padding-right: 20px;
                padding-left: 20px;
                margin: 0 auto;
                font-family: Montserrat;
              "
            >
              <p class="text-center" style="font-size: 24px">
                vKIRIROM PINE RESORT
              </p>
              <p class="text-center white--text" style="font-size: 18px">
                vKirirom Pine
                Resortは、多様なレジャーと教育活動、ユニークな宿泊施設、クメール料理と各国料理、キリロム国立公園の自然の景色を通じて、あらゆる種類の体験を提供するグランピングリゾートです。
                美しい現地の様子をバーチャルツアーでお楽しみください。
              </p>
              <p class="text-center white--text" style="font-size: 18px">
                vKirirom Pine Resort is an all-inclusive, eco-resort that offers
                all kinds of experiences through a vast variety of leisure and
                educational activities, unique and private lodging, luscious
                Khmer and international cuisines and the exquisite views of the
                divine nature of Kirirom National Park, the biggest pine forest
                in Cambodiaviding an affordable cloud hosting service for
                virtual tour.
              </p>
            </div>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
    <p
